html {
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0;
}

.header {
  display: flex;
  justify-content: space-between;
  padding-right: 50px;
  height: 60px;
}

.logo {
  position: relative;
  top: 10px;
  left: 50px;
}

.nav__list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
}

.nav__item {
  list-style-type: none;
}

.nav__link {
  display: block;
  margin-right: 20px;
  line-height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  text-decoration: none;
  color: black;
}

.nav__video {
  margin-right: 0;
}

.is-active,
.nav__link:hover {
  color: #00acdc;
}

.is-active::after {
  height: 4px;
  display: block;
  position: relative;
  top: -4.5px;
  content: "";
  border-radius: 8px;
  background-color: #00acdc;
}
